import React, { useCallback, useContext } from "react";
import { DashboardContext } from "../layout/DashboardLayout";
import {
  useAccountsServiceGetCurrentAccount,
  useAccountsServiceUpdateAccountDetails,
} from "../services/servicesComponents";
import { AccountClientDetails, V1Account } from "../services/servicesSchemas";
import { isUndefined } from "lodash";
import {
  Box,
  Button,
  Dialog,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from "@mui/material";
import { useFormik } from "formik";
import { regionCommune } from "../data";

export default function ProfileForm() {
  const { headers } = useContext(DashboardContext);
  const { data, isLoading } = useAccountsServiceGetCurrentAccount({ headers });

  if (isLoading) return <>loading...</>;

  return (
    <Dialog open={missingDetails(data?.account)} maxWidth="lg">
      <Box paddingX={10} paddingY={8}>
        {/* <DetailsForm account={data?.account} /> */}
      </Box>
    </Dialog>
  );
}
interface FormProps {
  account?: V1Account;
}

function DetailsForm(props: FormProps) {
  switch (props.account?.role) {
    case "ROLE_CLIENT":
      return <ClientDetailsForm account={props.account} />;

    default:
      return <>No form for role {props.account?.role}</>;
  }
}

function ClientDetailsForm(props: FormProps) {
  const { headers } = useContext(DashboardContext);
  const { mutate } = useAccountsServiceUpdateAccountDetails({});

  const handleSubmit = useCallback(
    async (clientDetails: AccountClientDetails) => {
      mutate({
        pathParams: { id: props.account!.id! },
        body: {
          clientDetails: {
            firstName: "Amaury Armando",
            lastName: "Diaz Caballero",
            rut: "My Rut",
            gender: "GENDER_MALE",
            maritalStatus: "MARITAL_STATUS_MARRIED",
            occupation: "Computer Scientist",
            street: "Valparaiso",
            streetNumber: "100",
            apartmentNumber: "40",
            region: "My region",
            commune: "My Commune",
            nationality: "Cuban",
            phoneNumber: "+491787938447",
            isapres: "isapress",
          },
        },
        headers,
      });
    },
    [headers, mutate, props.account]
  );

  const formik = useFormik({
    initialValues: {
      ...props.account?.details?.clientDetails,
    } as AccountClientDetails,
    onSubmit: handleSubmit,
  });

  return (
    <form onSubmit={formik.handleSubmit}>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            id="first_name"
            name="firstName"
            label="Nombre(s)"
            variant="filled"
            value={formik.values.firstName}
            onChange={formik.handleChange}
            error={formik.touched.firstName && Boolean(formik.errors.firstName)}
            helperText={formik.touched.firstName && formik.errors.firstName}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            id="last_name"
            name="lastName"
            label="Apellidos"
            variant="filled"
            value={formik.values.lastName}
            onChange={formik.handleChange}
            error={formik.touched.lastName && Boolean(formik.errors.lastName)}
            helperText={formik.touched.lastName && formik.errors.lastName}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            id="rut"
            name="rut"
            label="Rut"
            variant="filled"
            value={formik.values.rut}
            onChange={formik.handleChange}
            error={formik.touched.rut && Boolean(formik.errors.rut)}
            helperText={formik.touched.rut && formik.errors.rut}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="gender_label">Sexo</InputLabel>
            <Select
              id="gender"
              labelId="gender_label"
              name="gender"
              label="Sexo"
              value={formik.values.gender}
              defaultValue="GENDER_UNSPECIFIED"
              onChange={formik.handleChange}
              error={formik.touched.gender && Boolean(formik.errors.gender)}
            >
              <MenuItem value="GENDER_UNSPECIFIED">No especificado</MenuItem>
              <MenuItem value="GENDER_MALE">Masculino</MenuItem>
              <MenuItem value="GENDER_FEMALE">Femenino</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="marital_status_label">Estado Civil</InputLabel>
            <Select
              fullWidth
              id="marital_status"
              labelId="marital_status_label"
              name="maritalStatus"
              label="Estado Civil"
              value={formik.values.maritalStatus}
              defaultValue="MARITAL_STATUS_UNSPECIFIED"
              onChange={formik.handleChange}
              error={
                formik.touched.maritalStatus &&
                Boolean(formik.errors.maritalStatus)
              }
            >
              <MenuItem value="MARITAL_STATUS_UNSPECIFIED">
                No especificado
              </MenuItem>
              <MenuItem value="MARITAL_STATUS_SINGLE">Soltero(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_MARRIED">Casado(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_WIDOWED">Viudo(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_SEPARATED">Separado(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_DIVORCED">Divorciado(a)</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="region_label">Región</InputLabel>
            <Select
              fullWidth
              id="region"
              labelId="region_label"
              name="region"
              label="Región"
              value={formik.values.region}
              onChange={formik.handleChange}
              error={formik.touched.region && Boolean(formik.errors.region)}
            >
              {Object.keys(regionCommune).map((region) => (
                <MenuItem value={region} key={region}>
                  {region}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="commune_label">Comuna</InputLabel>
            <Select
              fullWidth
              id="commune"
              labelId="commune_label"
              name="maritalStatus"
              label="Comuna"
              value={formik.values.commune}
              onChange={formik.handleChange}
              error={formik.touched.commune && Boolean(formik.errors.commune)}
            >
              {regionCommune[formik.values.region || "No especificada"].map(
                (commune) => (
                  <MenuItem value={commune} key={commune}>
                    {commune}
                  </MenuItem>
                )
              )}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}></Grid>
        <Grid item xs={12} sm={6}></Grid>
      </Grid>

      <Button color="primary" variant="contained" fullWidth type="submit">
        Submit
      </Button>
    </form>
  );
}

function missingDetails(account?: V1Account) {
  switch (account?.role) {
    case "ROLE_CLIENT":
      return isUndefined(account.details?.clientDetails);
  }
  return true;
}
